* {
  box-sizing: border-box;
}

body {
  background: var(--background-body, #f5f5f5);
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.app-container {
  background: var(--background-body, #fff);
  flex-direction: column;
  max-width: 600px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
}

.app-header {
  background: var(--background-alt, #1a1a2e);
  color: #fff;
  z-index: 100;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.25rem;
  display: flex;
  position: sticky;
  top: 0;
}

.app-header h1 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
}

.header-btn {
  color: #fffc;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 8px;
  padding: .5rem;
  transition: background .2s;
}

.header-btn:hover {
  color: #fff;
  background: #ffffff1a;
}

.inventory-main {
  flex: 1;
  padding: 1rem 1rem 100px;
}

.loading-state {
  color: var(--text-muted, #666);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 1rem;
  display: flex;
}

.loading-spinner {
  border: 3px solid var(--border, #ddd);
  border-top-color: var(--links, #6db3f2);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.empty-state {
  text-align: center;
  color: var(--text-muted, #666);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 1rem;
  display: flex;
}

.empty-state svg {
  opacity: .3;
  margin-bottom: 1rem;
}

.empty-state p {
  margin: .25rem 0;
}

.empty-hint {
  opacity: .7;
  font-size: .9rem;
}

.inventory-list {
  flex-direction: column;
  gap: .75rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.inventory-item {
  background: var(--background, #fff);
  border: 1px solid var(--border, #e0e0e0);
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.item-info {
  flex: 1;
  min-width: 0;
}

.item-name {
  word-break: break-word;
  margin: 0 0 .25rem;
  font-size: 1rem;
  font-weight: 500;
}

.item-barcode {
  color: var(--text-muted, #888);
  font-family: monospace;
  font-size: .8rem;
}

.item-controls {
  align-items: center;
  gap: .5rem;
  display: flex;
}

.qty-btn {
  border: 1px solid var(--border, #ddd);
  background: var(--background-alt, #f5f5f5);
  cursor: pointer;
  color: var(--text-main, #333);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  font-size: 1.25rem;
  font-weight: 600;
  transition: background .2s;
  display: flex;
}

.qty-btn:hover {
  background: var(--border, #e0e0e0);
}

.qty-btn:active {
  transform: scale(.95);
}

.item-qty {
  text-align: center;
  min-width: 2rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.remove-btn {
  color: #dc3545;
  cursor: pointer;
  background: #dc35451a;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  margin-left: .5rem;
  transition: background .2s;
  display: flex;
}

.remove-btn:hover {
  background: #dc354533;
}

.bottom-action {
  background: none;
  justify-content: center;
  width: 100%;
  max-width: 400px;
  padding: 1rem;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.add-btn {
  background: var(--links, #6db3f2);
  color: #000;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  width: 100%;
  max-width: 300px;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 4px 20px #6db3f266;
}

.add-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px #6db3f280;
}

.add-btn:active {
  transform: scale(.98);
}

@media (prefers-color-scheme: dark) {
  body {
    background: #121212;
  }

  .app-container {
    background: #1e1e1e;
  }

  .app-header {
    background: #1a1a2e;
  }

  .inventory-item {
    background: #2d2d2d;
    border-color: #444;
  }

  .qty-btn {
    color: #e0e0e0;
    background: #3d3d3d;
    border-color: #555;
  }

  .qty-btn:hover {
    background: #4d4d4d;
  }

  .bottom-action {
    background: linear-gradient(#0000, #1e1e1e 30%);
  }

  .add-btn {
    color: #000;
    background: #6db3f2;
  }
}

@media (width <= 480px) {
  .app-header {
    padding: .875rem 1rem;
  }

  .app-header h1 {
    font-size: 1.1rem;
  }

  .inventory-item {
    padding: .875rem;
  }

  .item-name {
    font-size: .95rem;
  }

  .qty-btn {
    width: 32px;
    height: 32px;
    font-size: 1.1rem;
  }

  .remove-btn {
    width: 32px;
    height: 32px;
  }
}

